.nav-container{
    display: flex;
    height: 72px;
    align-items: center;
    background-color: #1d2020;
    margin: 0;
    box-shadow: 0 1px 3px rgba(0, 0, 0, .26);
    justify-content: space-between;
    z-index: 90;
}

.nav-left{
    display: flex;
    align-items: center;
}
.nav-right{
    margin-right: 40px;
    color: red;
}
.nav-right a{
    text-decoration: none;
    color: aliceblue;
    font-weight: bold;
    font-size: 20px;
}
.flash-logo{
    margin-left: 20px;
    height: 40px;
}

.flash-logo-text{
    font-family: 'Bangers' , cursive;
    font-size: 60px;
    letter-spacing: 1px;
    margin: 0 10px;
    color: #e7e6e7;
}

